$input-font-size: 20px;
$button-create-task-fs: 17px;

.inputWrapper {
	display: flex;
	justify-content: center;
}

.inputAddTask {
	width: 700px;
	height: 40px;
	background-color: transparent;
	border: 1px solid rgb(255, 255, 255);
	color: rgb(255, 255, 255);
	font-size: $input-font-size;
}

.buttonCreateTask {
	margin-left: 20px;
	width: 120px;
	font-size: $button-create-task-fs;
	font-weight: 600;
	letter-spacing: 2px;
	color: rgb(255, 255, 255);
	border: none;
	background: linear-gradient(120deg, rgb(0, 255, 137),rgb(30, 0, 255));
	cursor: pointer;

	&:disabled {
		background: rgb(101, 99, 99);;
	}
}

.invalidText {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
}